<template>
  <div class="flex-initial text-gray-700 text-center px-4 py-2 m-2">
    <form class="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4" v-on:submit="formSubmit">
      <input
        type="text"
        class="bg-white focus:outline-0 focus:shadow-outline border border-gray-300 rounded-lg py-2 px-4 block w-full appearance-none leading-normal"
        v-model="textToTranslate"
        placeholder="Enter a Word..."
      />
      <div class="relative">
        <select
          class="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
          v-model="language"
        >
          <option value="de">German</option>
          <option value="en">English</option>
          <option value="fr">French</option>
          <option value="tr">Turkish</option>
        </select>
        <div
          class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700"
        >
          <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20">
            <path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" />
          </svg>
        </div>
      </div>
      <br />
      <input
        class="shadow bg-purple-500 hover:bg-purple-400 focus:shadow-outline focus:outline-none text-white font-bold py-2 px-4 rounded cursor-pointer"
        type="submit"
        value="Translate"
      />
    </form>
    <div class id="TranslateForm">
      <h2 class="text-4xl">{{targetText}}</h2>
    </div>
    <div class="absolute bottom-0 right-0 bg-gray-100">
      <a href="https://vedatkokmen.com">Vedat Kökmen</a>
    </div>
  </div>
</template>

<script>
export default {
  props: ["targetText"],
  name: "translateForm",
  data() {
    return {
      textToTranslate: "",
      language: ""
    };
  },
  created() {
    this.language = "de";
  },
  methods: {
    formSubmit(e) {
      this.$emit("formSubmit", this.textToTranslate, this.language);
      e.preventDefault();
    }
  }
};
</script>

