<template>
  <div class="text-center text-xl text-base" id="app">
    <h1 class="text-4xl">Word Translator</h1>
    <h5 class="text-2xl">Powered by VueJs</h5>
    <TranslateForm v-on:formSubmit="sourceText"></TranslateForm>
    <TranslateOutput v-text="targetText"></TranslateOutput>
  </div>
</template>

<script>
import TranslateForm from "./components/TranslateForm";
import TranslateOutput from "./components/TranslateOutput";
export default {
  name: "app",
  apiKey:
    "trnsl.1.1.20190718T202401Z.4c8166022315bff3.f810a327053654acc4851c1a1fd3d9ced46ded48",
  components: {
    TranslateForm,
    TranslateOutput
  },
  data() {
    return {
      targetText: ""
    };
  },
  methods: {
    sourceText(text, language) {
      this.$http
        .get(
          "https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20190718T202401Z.4c8166022315bff3.f810a327053654acc4851c1a1fd3d9ced46ded48&lang=" +
            language +
            "&text=" +
            text
        )
        .then(response => {
          this.targetText = response.body.text[0];
        });
    }
  }
};
</script>

<style>
@import url("https://fonts.googleapis.com/css?family=Roboto+Condensed:400,700&display=swap&subset=cyrillic,latin-ext");

